import React, { useState, useEffect, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  TrendingUp, 
  TrendingDown, 
  Eye, 
  MousePointer, 
  DollarSign, 
  Users, 
  Calendar,
  Filter,
  Download,
  RefreshCw,
  Search,
  Plus,
  MoreVertical,
  Play,
  Pause,
  Edit,
  Copy,
  Trash2,
  BarChart3,
  PieChart,
  Target,
  Globe,
  Smartphone,
  Monitor,
  AlertCircle,
  CheckCircle,
  Settings,
  Loader,
  Phone,
  MapPin,
  ShoppingCart,
  Video,
  Percent,
  Clock,
  Zap,
  Star,
  Award,
  Activity,
  ArrowUpRight,
  ArrowDownRight,
  ChevronDown,
  ChevronUp,
  ExternalLink,
  Maximize2,
  Minimize2,
  Brain,
  Cpu,
} from 'lucide-react';

import AILoadingScreen from '../../AILoadingScreen';
import AIInsightsPanel from '../../AIInsightsPanel';
import AdvancedCharts from '../../AdvancedCharts';

// Enhanced interfaces for comprehensive campaign management
interface Campaign {
  id: string;
  name: string;
  status: 'ENABLED' | 'PAUSED' | 'REMOVED';
  type: string;
  subType: string;
  budget: number;
  spend: number;
  impressions: number;
  clicks: number;
  conversions: number;
  ctr: number;
  avgCpc: number;
  conversionRate: number;
  costPerConversion: number;
  qualityScore: number;
  impressionShare: number;
  targetLocation: string;
  bidStrategy: string;
  startDate: string;
  endDate: string;
  devicePerformance: {
    desktop: { impressions: number; clicks: number; cost: number };
    mobile: { impressions: number; clicks: number; cost: number };
    tablet: { impressions: number; clicks: number; cost: number };
  };
  audienceData: {
    ageGroups: { [key: string]: number };
    genders: { male: number; female: number; unknown: number };
    interests: string[];
  };
  geoPerformance: {
    [country: string]: { impressions: number; clicks: number; cost: number };
  };
}

interface Summary {
  totalSpend: number;
  totalClicks: number;
  totalImpressions: number;
  totalConversions: number;
  avgCpc: number;
  avgCtr: number;
  conversionRate: number;
  impressionShare: number;
  qualityScore: number;
  campaignTypes: Record<string, number>;
  statusBreakdown: {
    enabled: number;
    paused: number;
    removed: number;
  };
  performanceTrends: {
    impressions: { current: number; previous: number; change: number };
    clicks: { current: number; previous: number; change: number };
    cost: { current: number; previous: number; change: number };
    conversions: { current: number; previous: number; change: number };
  };
  topPerformingCampaigns: Campaign[];
  recommendations: {
    type: 'budget' | 'keyword' | 'audience' | 'bidding';
    title: string;
    description: string;
    impact: 'high' | 'medium' | 'low';
    campaignId?: string;
  }[];
}

interface ApiResponse {
  success: boolean;
  data?: {
    campaigns: Campaign[];
    summary: Summary;
    totalCount?: number;
    filteredCount?: number;
  };
  error?: string;
  note?: string;
  isDemo?: boolean;
}

interface GoogleAdsDashboardProps {
  selectedPeriod?: string;
  selectedCurrency?: string;
}

const GoogleAdsDashboard: React.FC<GoogleAdsDashboardProps> = ({ 
  selectedPeriod: propSelectedPeriod, 
  selectedCurrency: propSelectedCurrency 
}) => {
  // State management
  const [selectedPeriod, setSelectedPeriod] = useState(propSelectedPeriod || 'Last 30 days');
  const [selectedCurrency, setSelectedCurrency] = useState(propSelectedCurrency || 'EGP');
  const [campaigns, setCampaigns] = useState<Campaign[]>([]);
  const [summary, setSummary] = useState<Summary | null>(null);
  const [loading, setLoading] = useState(false); // ุชุบููุฑ ูู true ุฅูู false
  const [error, setError] = useState<string | null>(null);
  const [showAILoading, setShowAILoading] = useState(true);
  const [dataLoaded, setDataLoaded] = useState(false); // ุฅุถุงูุฉ state ุฌุฏูุฏ
  
  // Filter states
  const [searchTerm, setSearchTerm] = useState('');
  const [selectedType, setSelectedType] = useState('all');
  const [selectedStatus, setSelectedStatus] = useState('all');
  const [sortBy, setSortBy] = useState('spend');
  const [sortOrder, setSortOrder] = useState<'asc' | 'desc'>('desc');
  
  // UI states
  const [showFilters, setShowFilters] = useState(false);
  const [selectedCampaigns, setSelectedCampaigns] = useState<string[]>([]);
  const [viewMode, setViewMode] = useState<'table' | 'cards'>('table');
  const [autoRefresh, setAutoRefresh] = useState(false);
  const [refreshInterval, setRefreshInterval] = useState(30000); // 30 seconds

  // Handle AI Loading completion
  const handleAILoadingComplete = () => {
    setShowAILoading(false);
    // ุจุฏุก ุชุญููู ุงูุจูุงูุงุช ุจุนุฏ ุงูุชูุงุก AI Loading
    if (!dataLoaded) {
      fetchCampaignsData();
    }
  };

  // Available currencies
  const currencies = [
    { code: 'USD', name: 'US Dollar', symbol: '$' },
    { code: 'EGP', name: 'Egyptian Pound', symbol: 'EGP' },
    { code: 'SAR', name: 'Saudi Riyal', symbol: 'SAR' },
    { code: 'AED', name: 'UAE Dirham', symbol: 'AED' },
    { code: 'EUR', name: 'Euro', symbol: 'โฌ' },
    { code: 'GBP', name: 'British Pound', symbol: 'ยฃ' }
  ];

  // Time periods
  const timePeriods = [
    'Today',
    'Yesterday', 
    'Last 7 days',
    'Last 14 days',
    'Last 30 days',
    'Last 90 days',
    'This month',
    'Last month',
    'This quarter',
    'Last quarter',
    'This year',
    'Last year'
  ];

  // Helper function to get campaign type icon and styling
  const getCampaignTypeIcon = (type: string) => {
    const iconMap: Record<string, { icon: React.ReactNode; color: string; bgGradient: string }> = {
      'Performance Max': { 
        icon: <Target className="w-5 h-5" />, 
        color: 'text-purple-600', 
        bgGradient: 'bg-gradient-to-br from-purple-500 to-pink-600' 
      },
      'Search': { 
        icon: <Search className="w-5 h-5" />, 
        color: 'text-blue-600', 
        bgGradient: 'bg-gradient-to-br from-blue-500 to-cyan-600' 
      },
      'Display': { 
        icon: <Monitor className="w-5 h-5" />, 
        color: 'text-green-600', 
        bgGradient: 'bg-gradient-to-br from-green-500 to-emerald-600' 
      },
      'Shopping': { 
        icon: <ShoppingCart className="w-5 h-5" />, 
        color: 'text-orange-600', 
        bgGradient: 'bg-gradient-to-br from-orange-500 to-red-600' 
      },
      'Video': { 
        icon: <Video className="w-5 h-5" />, 
        color: 'text-red-600', 
        bgGradient: 'bg-gradient-to-br from-red-500 to-pink-600' 
      },
      'Call': { 
        icon: <Phone className="w-5 h-5" />, 
        color: 'text-indigo-600', 
        bgGradient: 'bg-gradient-to-br from-indigo-500 to-purple-600' 
      },
      'Local': { 
        icon: <MapPin className="w-5 h-5" />, 
        color: 'text-teal-600', 
        bgGradient: 'bg-gradient-to-br from-teal-500 to-cyan-600' 
      },
      'App': { 
        icon: <Smartphone className="w-5 h-5" />, 
        color: 'text-gray-600', 
        bgGradient: 'bg-gradient-to-br from-gray-500 to-slate-600' 
      },
      'Discovery': { 
        icon: <Globe className="w-5 h-5" />, 
        color: 'text-yellow-600', 
        bgGradient: 'bg-gradient-to-br from-yellow-500 to-orange-600' 
      }
    };
    
    return iconMap[type] || { 
      icon: <Target className="w-5 h-5" />, 
      color: 'text-gray-600', 
      bgGradient: 'bg-gradient-to-br from-gray-500 to-slate-600' 
    };
  };

  // Fetch campaigns data
  const fetchCampaignsData = async () => {
    try {
      setLoading(true);
      setError(null);
      
      console.log('๐ Fetching campaigns data...');
      
      const response = await fetch('/api/google-ads', {
        method: 'GET',
        headers: {
          'Content-Type': 'application/json',
        },
      });

      if (!response.ok) {
        throw new Error(`HTTP error! status: ${response.status}`);
      }

      const result: ApiResponse = await response.json();
      
      console.log('๐ฅ API Response:', result);
      
      if (result.success && result.data) {
        setCampaigns(result.data.campaigns || []);
        setSummary(result.data.summary || null);
        setDataLoaded(true);
        console.log('โ Data loaded successfully:', result.data.campaigns.length, 'campaigns');
      } else {
        throw new Error(result.error || 'Failed to fetch data');
      }
    } catch (error) {
      console.error('โ Error fetching campaigns:', error);
      setError(error instanceof Error ? error.message : 'Unknown error occurred');
      
      // Set fallback data
      setCampaigns([]);
      setSummary(null);
    } finally {
      setLoading(false);
    }
  };

  // Handle campaign actions
  const handleCampaignAction = async (campaignId: string, action: 'pause' | 'enable' | 'delete') => {
    try {
      const response = await fetch('/api/google-ads', {
        method: 'PUT',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({ campaignId, action }),
      });

      const result = await response.json();
      
      if (result.success) {
        console.log(`โ Campaign ${action}d successfully`);
        // Refresh data after action
        fetchCampaignsData();
      } else {
        console.error(`โ Failed to ${action} campaign:`, result.error);
      }
    } catch (error) {
      console.error(`โ Error ${action}ing campaign:`, error);
    }
  };

  // Filter and sort campaigns
  const filteredAndSortedCampaigns = useMemo(() => {
    let filtered = campaigns.filter(campaign => {
      const matchesSearch = campaign.name.toLowerCase().includes(searchTerm.toLowerCase());
      const matchesType = selectedType === 'all' || campaign.type.toLowerCase().includes(selectedType.toLowerCase());
      const matchesStatus = selectedStatus === 'all' || campaign.status.toLowerCase() === selectedStatus.toLowerCase();
      
      return matchesSearch && matchesType && matchesStatus;
    });

    // Sort campaigns
    filtered.sort((a, b) => {
      let aValue: number, bValue: number;
      
      switch (sortBy) {
        case 'name':
          return sortOrder === 'asc' 
            ? a.name.localeCompare(b.name)
            : b.name.localeCompare(a.name);
        case 'spend':
          aValue = a.spend;
          bValue = b.spend;
          break;
        case 'clicks':
          aValue = a.clicks;
          bValue = b.clicks;
          break;
        case 'impressions':
          aValue = a.impressions;
          bValue = b.impressions;
          break;
        case 'ctr':
          aValue = a.ctr;
          bValue = b.ctr;
          break;
        case 'conversions':
          aValue = a.conversions;
          bValue = b.conversions;
          break;
        default:
          aValue = a.spend;
          bValue = b.spend;
      }
      
      return sortOrder === 'asc' ? aValue - bValue : bValue - aValue;
    });

    return filtered;
  }, [campaigns, selectedType, selectedStatus, searchTerm, sortBy, sortOrder]);

  // Format currency
  const formatCurrency = (amount: number): string => {
    const currencySymbol = currencies.find(c => c.code === selectedCurrency)?.symbol || selectedCurrency;
    return `${currencySymbol} ${amount.toLocaleString('en-US', {
      minimumFractionDigits: 2,
      maximumFractionDigits: 2
    })}`;
  };

  // Format percentage
  const formatPercentage = (value: number): string => {
    return `${value.toFixed(2)}%`;
  };

  // Format number
  const formatNumber = (value: number): string => {
    return value.toLocaleString('en-US');
  };

  // Get trend icon and color
  const getTrendIcon = (change: number) => {
    if (change > 0) {
      return { icon: <ArrowUpRight className="w-4 h-4" />, color: 'text-green-600', bgColor: 'bg-green-100' };
    } else if (change < 0) {
      return { icon: <ArrowDownRight className="w-4 h-4" />, color: 'text-red-600', bgColor: 'bg-red-100' };
    } else {
      return { icon: <Activity className="w-4 h-4" />, color: 'text-gray-600', bgColor: 'bg-gray-100' };
    }
  };

  // Effects
  useEffect(() => {
    // ุชุญููู ุงูุจูุงูุงุช ููุท ุฅุฐุง ูู ูุชู ุชุญููููุง ูู ูุจู ูุงูุชูู AI Loading
    if (!showAILoading && !dataLoaded) {
      fetchCampaignsData();
    }
  }, [showAILoading, dataLoaded]);

  useEffect(() => {
    // ุฅุนุงุฏุฉ ุชุญููู ุงูุจูุงูุงุช ุนูุฏ ุชุบููุฑ ุงูููุงุชุฑ (ููุท ุฅุฐุง ุชู ุชุญููู ุงูุจูุงูุงุช ูู ูุจู)
    if (dataLoaded) {
      fetchCampaignsData();
    }
  }, [selectedType, selectedStatus, searchTerm, selectedPeriod]);

  useEffect(() => {
    if (autoRefresh && dataLoaded) {
      const interval = setInterval(() => {
        fetchCampaignsData();
        console.log('๐ Auto-refresh triggered');
      }, refreshInterval);
      return () => clearInterval(interval);
    }
  }, [autoRefresh, refreshInterval, dataLoaded]);

  // Show AI Loading Screen first
  if (showAILoading) {
    return <AILoadingScreen onComplete={handleAILoadingComplete} />;
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-50 via-blue-50 to-indigo-100">
      {/* Animated Background */}
      <div className="fixed inset-0 overflow-hidden pointer-events-none">
        <div className="absolute -top-40 -right-40 w-80 h-80 bg-gradient-to-br from-blue-400/20 to-purple-600/20 rounded-full blur-3xl animate-pulse"></div>
        <div className="absolute -bottom-40 -left-40 w-80 h-80 bg-gradient-to-br from-green-400/20 to-blue-600/20 rounded-full blur-3xl animate-pulse delay-1000"></div>
        <div className="absolute top-1/2 left-1/2 transform -translate-x-1/2 -translate-y-1/2 w-96 h-96 bg-gradient-to-br from-purple-400/10 to-pink-600/10 rounded-full blur-3xl animate-pulse delay-2000"></div>
      </div>

      <div className="relative z-10 p-6 space-y-8">
        {/* Header Section */}
        <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
          <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6">
            <div className="space-y-2">
              <div className="flex items-center gap-3">
                <div className="p-3 bg-gradient-to-br from-blue-500 to-purple-600 rounded-2xl shadow-lg">
                  <Target className="w-8 h-8 text-white" />
                </div>
                <div>
                  <h1 className="text-4xl font-bold bg-gradient-to-r from-gray-900 via-blue-800 to-purple-800 bg-clip-text text-transparent">
                    Google Ads Dashboard
                  </h1>
                  <p className="text-gray-600 font-medium">AI-Powered Campaign Management</p>
                </div>
              </div>
              <div className="flex items-center gap-4 text-sm text-gray-500">
                <div className="flex items-center gap-2">
                  <Calendar className="w-4 h-4" />
                  <span>{selectedPeriod}</span>
                </div>
                <div className="flex items-center gap-2">
                  <DollarSign className="w-4 h-4" />
                  <span>{currencies.find(c => c.code === selectedCurrency)?.name}</span>
                </div>
                <div className="flex items-center gap-2">
                  <Users className="w-4 h-4" />
                  <span>{campaigns.length} Campaigns</span>
                </div>
                {autoRefresh && (
                  <div className="flex items-center gap-2 text-green-600">
                    <RefreshCw className="w-4 h-4 animate-spin" />
                    <span>Auto-refresh enabled</span>
                  </div>
                )}
              </div>
            </div>

            {/* Controls */}
            <div className="flex flex-wrap items-center gap-4">
              {/* Period Selector */}
              <select
                value={selectedPeriod}
                onChange={(e) => setSelectedPeriod(e.target.value)}
                className="px-4 py-3 bg-white/80 backdrop-blur-sm border border-gray-200 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent shadow-lg"
              >
                {timePeriods.map(period => (
                  <option key={period} value={period}>{period}</option>
                ))}
              </select>

              {/* Currency Selector */}
              <select
                value={selectedCurrency}
                onChange={(e) => setSelectedCurrency(e.target.value)}
                className="px-4 py-3 bg-white/80 backdrop-blur-sm border border-gray-200 rounded-xl text-sm font-medium focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent shadow-lg"
              >
                {currencies.map(currency => (
                  <option key={currency.code} value={currency.code}>
                    {currency.symbol} {currency.name}
                  </option>
                ))}
              </select>

              {/* Auto Refresh Toggle */}
              <button
                onClick={() => setAutoRefresh(!autoRefresh)}
                className={`px-4 py-3 rounded-xl text-sm font-medium transition-all duration-300 shadow-lg ${
                  autoRefresh 
                    ? 'bg-gradient-to-r from-green-500 to-emerald-600 text-white' 
                    : 'bg-white/80 backdrop-blur-sm border border-gray-200 text-gray-700 hover:bg-gray-50'
                }`}
              >
                <RefreshCw className={`w-4 h-4 ${autoRefresh ? 'animate-spin' : ''}`} />
              </button>

              {/* Refresh Button */}
              <button
                onClick={() => {
                  fetchCampaignsData();
                }}
                disabled={loading}
                className="px-6 py-3 bg-gradient-to-r from-blue-500 to-purple-600 text-white rounded-xl font-medium hover:from-blue-600 hover:to-purple-700 focus:outline-none focus:ring-2 focus:ring-blue-500 focus:ring-offset-2 disabled:opacity-50 transition-all duration-300 shadow-lg"
              >
                <RefreshCw className={`w-4 h-4 mr-2 ${loading ? 'animate-spin' : ''}`} />
                Refresh
              </button>
            </div>
          </div>
        </div>

        {/* Loading State */}
        {loading && (
          <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
            <div className="flex items-center justify-center space-x-4">
              <Loader className="w-8 h-8 animate-spin text-blue-600" />
              <span className="text-lg font-medium text-gray-700">Loading campaign data...</span>
            </div>
          </div>
        )}

        {/* Error State */}
        {error && (
          <div className="bg-red-50/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-red-200/50 p-8">
            <div className="flex items-center space-x-4">
              <AlertCircle className="w-8 h-8 text-red-600" />
              <div>
                <h3 className="text-lg font-medium text-red-800">Error Loading Data</h3>
                <p className="text-red-600">{error}</p>
              </div>
            </div>
          </div>
        )}

        {/* Show content only when data is loaded and not loading */}
        {!loading && dataLoaded && (
          <>
            {/* KPI Cards */}
            {summary && (
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                {/* Total Impressions */}
                <div className="group bg-white/80 backdrop-blur-xl rounded-2xl shadow-xl border border-white/20 p-6 hover:shadow-2xl transition-all duration-500 hover:scale-105">
                  <div className="flex items-center justify-between">
                    <div className="space-y-2">
                      <p className="text-sm font-medium text-gray-600">Total Impressions</p>
                      <p className="text-3xl font-bold text-gray-900">{formatNumber(summary?.totalImpressions || 0)}</p>
                      <div className="flex items-center gap-2">
                        {(() => {
                          const trend = getTrendIcon(summary?.performanceTrends?.impressions?.change || 0);
                          return (
                            <>
                              <div className={`p-1 rounded-full ${trend.bgColor}`}>
                                <div className={trend.color}>{trend.icon}</div>
                              </div>
                              <span className={`text-sm font-medium ${trend.color}`}>
                                {summary?.performanceTrends?.impressions?.change ? (summary.performanceTrends.impressions.change > 0 ? '+' : '') : ''}
                                {formatPercentage(summary?.performanceTrends?.impressions?.change || 0)}
                              </span>
                            </>
                          );
                        })()}
                      </div>
                    </div>
                    <div className="p-4 bg-gradient-to-br from-blue-500 to-cyan-600 rounded-2xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                      <Eye className="w-8 h-8 text-white" />
                    </div>
                  </div>
                </div>

                {/* Total Clicks */}
                <div className="group bg-white/80 backdrop-blur-xl rounded-2xl shadow-xl border border-white/20 p-6 hover:shadow-2xl transition-all duration-500 hover:scale-105">
                  <div className="flex items-center justify-between">
                    <div className="space-y-2">
                      <p className="text-sm font-medium text-gray-600">Total Clicks</p>
                      <p className="text-3xl font-bold text-gray-900">{formatNumber(summary?.totalClicks || 0)}</p>
                      <div className="flex items-center gap-2">
                        {(() => {
                          const trend = getTrendIcon(summary?.performanceTrends?.clicks?.change || 0);
                          return (
                            <>
                              <div className={`p-1 rounded-full ${trend.bgColor}`}>
                                <div className={trend.color}>{trend.icon}</div>
                              </div>
                              <span className={`text-sm font-medium ${trend.color}`}>
                                {summary?.performanceTrends?.clicks?.change ? (summary.performanceTrends.clicks.change > 0 ? '+' : '') : ''}
                                {formatPercentage(summary?.performanceTrends?.clicks?.change || 0)}
                              </span>
                            </>
                          );
                        })()}
                      </div>
                    </div>
                    <div className="p-4 bg-gradient-to-br from-green-500 to-emerald-600 rounded-2xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                      <MousePointer className="w-8 h-8 text-white" />
                    </div>
                  </div>
                </div>

                {/* Total Cost */}
                <div className="group bg-white/80 backdrop-blur-xl rounded-2xl shadow-xl border border-white/20 p-6 hover:shadow-2xl transition-all duration-500 hover:scale-105">
                  <div className="flex items-center justify-between">
                    <div className="space-y-2">
                      <p className="text-sm font-medium text-gray-600">Total Cost</p>
                      <p className="text-3xl font-bold text-gray-900">{formatCurrency(summary?.totalSpend || 0)}</p>
                      <div className="flex items-center gap-2">
                        {(() => {
                          const trend = getTrendIcon(summary?.performanceTrends?.cost?.change || 0);
                          return (
                            <>
                              <div className={`p-1 rounded-full ${trend.bgColor}`}>
                                <div className={trend.color}>{trend.icon}</div>
                              </div>
                              <span className={`text-sm font-medium ${trend.color}`}>
                                {summary?.performanceTrends?.cost?.change ? (summary.performanceTrends.cost.change > 0 ? '+' : '') : ''}
                                {formatPercentage(summary?.performanceTrends?.cost?.change || 0)}
                              </span>
                            </>
                          );
                        })()}
                      </div>
                    </div>
                    <div className="p-4 bg-gradient-to-br from-orange-500 to-red-600 rounded-2xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                      <DollarSign className="w-8 h-8 text-white" />
                    </div>
                  </div>
                </div>

                {/* Total Conversions */}
                <div className="group bg-white/80 backdrop-blur-xl rounded-2xl shadow-xl border border-white/20 p-6 hover:shadow-2xl transition-all duration-500 hover:scale-105">
                  <div className="flex items-center justify-between">
                    <div className="space-y-2">
                      <p className="text-sm font-medium text-gray-600">Total Conversions</p>
                      <p className="text-3xl font-bold text-gray-900">{formatNumber(summary?.totalConversions || 0)}</p>
                      <div className="flex items-center gap-2">
                        {(() => {
                          const trend = getTrendIcon(summary?.performanceTrends?.conversions?.change || 0);
                          return (
                            <>
                              <div className={`p-1 rounded-full ${trend.bgColor}`}>
                                <div className={trend.color}>{trend.icon}</div>
                              </div>
                              <span className={`text-sm font-medium ${trend.color}`}>
                                {summary?.performanceTrends?.conversions?.change ? (summary.performanceTrends.conversions.change > 0 ? '+' : '') : ''}
                                {formatPercentage(summary?.performanceTrends?.conversions?.change || 0)}
                              </span>
                            </>
                          );
                        })()}
                      </div>
                    </div>
                    <div className="p-4 bg-gradient-to-br from-purple-500 to-pink-600 rounded-2xl shadow-lg group-hover:scale-110 transition-transform duration-300">
                      <Target className="w-8 h-8 text-white" />
                    </div>
                  </div>
                </div>
              </div>
            )}

            {/* AI Insights Panel */}
            <AIInsightsPanel campaigns={campaigns} summary={summary} />

            {/* Advanced Charts */}
            <AdvancedCharts campaigns={campaigns} summary={summary} />

            {/* Campaigns Table/Cards */}
            <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
              <div className="flex flex-col lg:flex-row lg:items-center lg:justify-between gap-6 mb-8">
                <div>
                  <h2 className="text-2xl font-bold text-gray-900">Campaign Management</h2>
                  <p className="text-gray-600">Monitor and optimize your Google Ads campaigns</p>
                </div>

                {/* Filters and Controls */}
                <div className="flex flex-wrap items-center gap-4">
                  {/* Search */}
                  <div className="relative">
                    <Search className="absolute left-3 top-1/2 transform -translate-y-1/2 text-gray-400 w-4 h-4" />
                    <input
                      type="text"
                      placeholder="Search campaigns..."
                      value={searchTerm}
                      onChange={(e) => setSearchTerm(e.target.value)}
                      className="pl-10 pr-4 py-2 bg-white/80 backdrop-blur-sm border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300"
                    />
                  </div>

                  {/* Type Filter */}
                  <select
                    value={selectedType}
                    onChange={(e) => setSelectedType(e.target.value)}
                    className="px-4 py-2 bg-white/80 backdrop-blur-sm border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300"
                  >
                    <option value="all">All Types</option>
                    <option value="performance max">Performance Max</option>
                    <option value="search">Search</option>
                    <option value="shopping">Shopping</option>
                    <option value="display">Display</option>
                    <option value="video">Video</option>
                    <option value="local">Local</option>
                  </select>

                  {/* Status Filter */}
                  <select
                    value={selectedStatus}
                    onChange={(e) => setSelectedStatus(e.target.value)}
                    className="px-4 py-2 bg-white/80 backdrop-blur-sm border border-gray-300 rounded-xl focus:outline-none focus:ring-2 focus:ring-blue-500 focus:border-transparent transition-all duration-300"
                  >
                    <option value="all">All Status</option>
                    <option value="enabled">Enabled</option>
                    <option value="paused">Paused</option>
                    <option value="removed">Removed</option>
                  </select>

                  {/* View Mode Toggle */}
                  <div className="flex items-center bg-gray-100 rounded-xl p-1">
                    <button
                      onClick={() => setViewMode('table')}
                      className={`px-3 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
                        viewMode === 'table'
                          ? 'bg-white text-blue-600 shadow-sm'
                          : 'text-gray-600 hover:text-gray-900'
                      }`}
                    >
                      Table
                    </button>
                    <button
                      onClick={() => setViewMode('cards')}
                      className={`px-3 py-2 rounded-lg text-sm font-medium transition-all duration-200 ${
                        viewMode === 'cards'
                          ? 'bg-white text-blue-600 shadow-sm'
                          : 'text-gray-600 hover:text-gray-900'
                      }`}
                    >
                      Cards
                    </button>
                  </div>
                </div>
              </div>

              {/* Campaigns Display */}
              <div className="space-y-6">
                {viewMode === 'table' ? (
                  /* Table View */
                  <div className="overflow-x-auto">
                    <table className="w-full">
                      <thead>
                        <tr className="border-b border-gray-200">
                          <th className="text-left py-4 px-4 font-semibold text-gray-900">Campaign</th>
                          <th className="text-left py-4 px-4 font-semibold text-gray-900">Type</th>
                          <th className="text-left py-4 px-4 font-semibold text-gray-900">Status</th>
                          <th className="text-right py-4 px-4 font-semibold text-gray-900">Impressions</th>
                          <th className="text-right py-4 px-4 font-semibold text-gray-900">Clicks</th>
                          <th className="text-right py-4 px-4 font-semibold text-gray-900">Cost</th>
                          <th className="text-right py-4 px-4 font-semibold text-gray-900">CTR</th>
                          <th className="text-right py-4 px-4 font-semibold text-gray-900">Conversions</th>
                          <th className="text-center py-4 px-4 font-semibold text-gray-900">Actions</th>
                        </tr>
                      </thead>
                      <tbody>
                        {filteredAndSortedCampaigns.map((campaign, index) => {
                          const typeIcon = getCampaignTypeIcon(campaign.type);
                          return (
                            <tr 
                              key={campaign.id}
                              className="border-b border-gray-100 hover:bg-blue-50/50 transition-colors duration-200"
                            >
                              <td className="py-4 px-4">
                                <div className="flex items-center gap-3">
                                  <div className={`p-2 rounded-lg ${typeIcon.bgGradient}`}>
                                    <div className="text-white">
                                      {typeIcon.icon}
                                    </div>
                                  </div>
                                  <div>
                                    <div className="font-medium text-gray-900">{campaign.name}</div>
                                    <div className="text-sm text-gray-500">{campaign.targetLocation}</div>
                                  </div>
                                </div>
                              </td>
                              <td className="py-4 px-4 text-gray-700">{campaign.type}</td>
                              <td className="py-4 px-4">
                                <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-semibold ${
                                  campaign.status === 'ENABLED' 
                                    ? 'bg-green-100 text-green-800' 
                                    : campaign.status === 'PAUSED' 
                                    ? 'bg-yellow-100 text-yellow-800' 
                                    : 'bg-red-100 text-red-800'
                                }`}>
                                  {campaign.status}
                                </span>
                              </td>
                              <td className="py-4 px-4 text-right font-medium text-gray-900">{formatNumber(campaign.impressions)}</td>
                              <td className="py-4 px-4 text-right font-medium text-gray-900">{formatNumber(campaign.clicks)}</td>
                              <td className="py-4 px-4 text-right font-medium text-gray-900">{formatCurrency(campaign.spend)}</td>
                              <td className="py-4 px-4 text-right font-medium text-gray-900">{formatPercentage(campaign.ctr)}</td>
                              <td className="py-4 px-4 text-right font-medium text-gray-900">{formatNumber(campaign.conversions)}</td>
                              <td className="py-4 px-4">
                                <div className="flex items-center justify-center gap-2">
                                  {campaign.status === 'ENABLED' ? (
                                    <button
                                      onClick={() => handleCampaignAction(campaign.id, 'pause')}
                                      className="p-2 text-yellow-600 hover:text-yellow-800 hover:bg-yellow-100 rounded-lg transition-all duration-200"
                                      title="Pause Campaign"
                                    >
                                      <Pause className="w-4 h-4" />
                                    </button>
                                  ) : campaign.status === 'PAUSED' ? (
                                    <button
                                      onClick={() => handleCampaignAction(campaign.id, 'enable')}
                                      className="p-2 text-green-600 hover:text-green-800 hover:bg-green-100 rounded-lg transition-all duration-200"
                                      title="Enable Campaign"
                                    >
                                      <Play className="w-4 h-4" />
                                    </button>
                                  ) : null}
                                  
                                  <button
                                    onClick={() => handleCampaignAction(campaign.id, 'delete')}
                                    className="p-2 text-red-600 hover:text-red-800 hover:bg-red-100 rounded-lg transition-all duration-200"
                                    title="Delete Campaign"
                                  >
                                    <Trash2 className="w-4 h-4" />
                                  </button>
                                </div>
                              </td>
                            </tr>
                          );
                        })}
                      </tbody>
                    </table>
                  </div>
                ) : (
                  /* Cards View */
                  <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-6">
                    {filteredAndSortedCampaigns.map((campaign, index) => {
                      const typeIcon = getCampaignTypeIcon(campaign.type);
                      
                      return (
                        <div 
                          key={campaign.id}
                          className="group bg-white/80 backdrop-blur-sm rounded-2xl shadow-lg border border-gray-200/50 p-6 hover:shadow-xl transition-all duration-300 hover:scale-105"
                          style={{ animationDelay: `${index * 100}ms` }}
                        >
                          {/* Card Header */}
                          <div className="flex items-start justify-between mb-4">
                            <div className="flex items-center gap-3">
                              <div className={`p-3 rounded-xl shadow-md ${typeIcon.bgGradient}`}>
                                <div className="text-white">
                                  {typeIcon.icon}
                                </div>
                              </div>
                              <div>
                                <h3 className="font-semibold text-gray-900 group-hover:text-blue-600 transition-colors duration-200">
                                  {campaign.name}
                                </h3>
                                <p className="text-sm text-gray-500">{campaign.type}</p>
                              </div>
                            </div>
                            <span className={`inline-flex items-center px-2 py-1 rounded-full text-xs font-semibold ${
                              campaign.status === 'ENABLED' 
                                ? 'bg-green-100 text-green-800' 
                                : campaign.status === 'PAUSED' 
                                ? 'bg-yellow-100 text-yellow-800' 
                                : 'bg-red-100 text-red-800'
                            }`}>
                              {campaign.status}
                            </span>
                          </div>

                          {/* Card Metrics */}
                          <div className="grid grid-cols-2 gap-4 mb-4">
                            <div className="space-y-1">
                              <p className="text-xs text-gray-500">Impressions</p>
                              <p className="font-semibold text-gray-900">{formatNumber(campaign.impressions)}</p>
                            </div>
                            <div className="space-y-1">
                              <p className="text-xs text-gray-500">Clicks</p>
                              <p className="font-semibold text-gray-900">{formatNumber(campaign.clicks)}</p>
                            </div>
                            <div className="space-y-1">
                              <p className="text-xs text-gray-500">Cost</p>
                              <p className="font-semibold text-gray-900">{formatCurrency(campaign.spend)}</p>
                            </div>
                            <div className="space-y-1">
                              <p className="text-xs text-gray-500">CTR</p>
                              <p className="font-semibold text-gray-900">{formatPercentage(campaign.ctr)}</p>
                            </div>
                          </div>

                          {/* Card Actions */}
                          <div className="flex items-center justify-between pt-4 border-t border-gray-200">
                            <div className="flex items-center gap-2">
                              {campaign.status === 'ENABLED' ? (
                                <button
                                  onClick={() => handleCampaignAction(campaign.id, 'pause')}
                                  className="p-2 text-yellow-600 hover:text-yellow-800 hover:bg-yellow-100 rounded-lg transition-all duration-200"
                                  title="Pause Campaign"
                                >
                                  <Pause className="w-4 h-4" />
                                </button>
                              ) : campaign.status === 'PAUSED' ? (
                                <button
                                  onClick={() => handleCampaignAction(campaign.id, 'enable')}
                                  className="p-2 text-green-600 hover:text-green-800 hover:bg-green-100 rounded-lg transition-all duration-200"
                                  title="Enable Campaign"
                                >
                                  <Play className="w-4 h-4" />
                                </button>
                              ) : null}
                              
                              <button
                                onClick={() => handleCampaignAction(campaign.id, 'delete')}
                                className="p-2 text-red-600 hover:text-red-800 hover:bg-red-100 rounded-lg transition-all duration-200"
                                title="Delete Campaign"
                              >
                                <Trash2 className="w-4 h-4" />
                              </button>
                            </div>
                            
                            <div className="text-xs text-gray-500">
                              Quality Score: {campaign.qualityScore}/10
                            </div>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                )}

                {!loading && filteredAndSortedCampaigns.length === 0 && (
                  <div className="text-center py-20">
                    <div className="w-16 h-16 bg-gray-100 rounded-2xl flex items-center justify-center mx-auto mb-4">
                      <Search className="w-8 h-8 text-gray-400" />
                    </div>
                    <p className="text-gray-500 font-medium">No campaigns found matching your criteria.</p>
                    <button
                      onClick={() => {
                        setSearchTerm('');
                        setSelectedType('all');
                        setSelectedStatus('all');
                      }}
                      className="mt-4 px-6 py-3 bg-blue-600 text-white rounded-xl font-medium hover:bg-blue-700 transition-colors duration-200"
                    >
                      Clear Filters
                    </button>
                  </div>
                )}
              </div>
            </div>

            {/* AI Recommendations */}
            {summary?.recommendations && summary.recommendations.length > 0 && (
              <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
                <div className="flex items-center gap-3 mb-6">
                  <div className="p-3 bg-gradient-to-br from-purple-500 to-pink-600 rounded-2xl shadow-lg">
                    <Zap className="w-6 h-6 text-white" />
                  </div>
                  <div>
                    <h2 className="text-2xl font-bold text-gray-900">AI Recommendations</h2>
                    <p className="text-gray-600">Optimize your campaigns with AI-powered insights</p>
                  </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
                  {summary.recommendations.map((rec, index) => (
                    <div 
                      key={index}
                      className="group bg-gradient-to-br from-white to-blue-50/50 rounded-2xl p-6 border border-blue-200/50 hover:shadow-lg transition-all duration-300"
                    >
                      <div className="flex items-start gap-4">
                        <div className={`p-2 rounded-xl ${
                          rec.impact === 'high' ? 'bg-red-100 text-red-600' :
                          rec.impact === 'medium' ? 'bg-yellow-100 text-yellow-600' :
                          'bg-green-100 text-green-600'
                        }`}>
                          {rec.type === 'budget' ? <DollarSign className="w-5 h-5" /> :
                           rec.type === 'keyword' ? <Search className="w-5 h-5" /> :
                           rec.type === 'audience' ? <Users className="w-5 h-5" /> :
                           <Settings className="w-5 h-5" />}
                        </div>
                        <div className="flex-1 space-y-2">
                          <div className="flex items-center gap-2">
                            <h3 className="font-semibold text-gray-900">{rec.title}</h3>
                            <span className={`px-2 py-1 rounded-full text-xs font-medium ${
                              rec.impact === 'high' ? 'bg-red-100 text-red-700' :
                              rec.impact === 'medium' ? 'bg-yellow-100 text-yellow-700' :
                              'bg-green-100 text-green-700'
                            }`}>
                              {rec.impact} impact
                            </span>
                          </div>
                          <p className="text-gray-600 text-sm">{rec.description}</p>
                          <button className="text-blue-600 hover:text-blue-800 text-sm font-medium flex items-center gap-1 group-hover:gap-2 transition-all duration-200">
                            Apply Recommendation
                            <ExternalLink className="w-4 h-4" />
                          </button>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            )}

            {/* Performance Summary */}
            {summary && (
              <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                {/* Campaign Types Distribution */}
                <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
                  <div className="flex items-center gap-3 mb-6">
                    <div className="p-3 bg-gradient-to-br from-blue-500 to-cyan-600 rounded-2xl shadow-lg">
                      <PieChart className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h2 className="text-xl font-bold text-gray-900">Campaign Types</h2>
                      <p className="text-gray-600">Distribution by campaign type</p>
                    </div>
                  </div>

                  <div className="space-y-4">
                    {summary?.campaignTypes ? Object.entries(summary.campaignTypes).map(([type, count]) => {
                      const typeIcon = getCampaignTypeIcon(type);
                      const percentage = (count / campaigns.length) * 100;
                      
                      return (
                        <div key={type} className="flex items-center gap-4">
                          <div className={`p-2 rounded-xl ${typeIcon.bgGradient}`}>
                            <div className="text-white">
                              {typeIcon.icon}
                            </div>
                          </div>
                          <div className="flex-1">
                            <div className="flex items-center justify-between mb-1">
                              <span className="font-medium text-gray-900">{type}</span>
                              <span className="text-sm text-gray-600">{count} campaigns</span>
                            </div>
                            <div className="w-full bg-gray-200 rounded-full h-2">
                              <div 
                                className={`h-2 rounded-full ${typeIcon.bgGradient}`}
                                style={{ width: `${percentage}%` }}
                              ></div>
                            </div>
                          </div>
                        </div>
                      );
                    }) : null}
                  </div>
                </div>

                {/* Top Performing Campaigns */}
                <div className="bg-white/80 backdrop-blur-xl rounded-3xl shadow-2xl border border-white/20 p-8">
                  <div className="flex items-center gap-3 mb-6">
                    <div className="p-3 bg-gradient-to-br from-green-500 to-emerald-600 rounded-2xl shadow-lg">
                      <Award className="w-6 h-6 text-white" />
                    </div>
                    <div>
                      <h2 className="text-xl font-bold text-gray-900">Top Performers</h2>
                      <p className="text-gray-600">Best converting campaigns</p>
                    </div>
                  </div>

                  <div className="space-y-4">
                    {summary?.topPerformingCampaigns?.slice(0, 3).map((campaign, index) => {
                      const typeIcon = getCampaignTypeIcon(campaign.type);
                      return (
                        <div key={campaign.id} className="flex items-center gap-4 p-4 bg-gradient-to-r from-green-50 to-emerald-50 rounded-xl border border-green-200/50">
                          <div className="flex items-center gap-3 flex-1">
                            <div className={`p-2 rounded-lg ${typeIcon.bgGradient}`}>
                              <div className="text-white">
                                {typeIcon.icon}
                              </div>
                            </div>
                            <div>
                              <h3 className="font-medium text-gray-900">{campaign.name}</h3>
                              <p className="text-sm text-gray-600">{formatPercentage(campaign.conversionRate)} conversion rate</p>
                            </div>
                          </div>
                          <div className="text-right">
                            <p className="font-semibold text-gray-900">{formatNumber(campaign.conversions)}</p>
                            <p className="text-sm text-gray-600">conversions</p>
                          </div>
                        </div>
                      );
                    })}
                  </div>
                </div>
              </div>
            )}
          </>
        )}
      </div>
    </div>
  );
};

export default GoogleAdsDashboard;

